$themes:( green-theme:( color:#27ae60), blue-theme:( color:#2980b9), red-theme:( color: #c0392b), orange-theme:( color: #d35400), purble-theme:( color: #800080));
@each $theme,
$map in $themes {
    .#{$theme} {
        $color: map-get($map, color);
        .page .page-head .page-title {
            color: $color;
        }
        .header .header-icon:hover {
            color: $color;
        }
        .header .usermenu-container .dropdown-menu .user-info h3 {
            color: $color;
        }
        .header .usermenu-container .username .user {
            color: $color;
        }
    }
}